import { updateSiteModelChecklist } from "../../actions/cbChecklist_Actions";
import { updateAppSettings } from "../../actions/foundationActions";
import { saveChecklistGoal } from "../../containers/cbChecklist/checkListMethods";
import store from "../../store";
import { randNum } from "../../utilities/utilityFunctions"

const editGoal = (goal) =>{
    store.dispatch(updateSiteModelChecklist({editGoal:goal}));
    store.dispatch(updateAppSettings({showDefaultModal:1,"modalModuleName":"components/cbChecklist/GoalForm","modalTitle":"New Goal"}));
}


const GoalBox = ({goal}) => {
    return <td key={"label" + goal.id + randNum(1,10000)}> 
    <div className="row">
        <div className="col-9">{goal.label}</div>
        <div className="col-2">
            <span className="badge text-bg-info" onClick={()=>{editGoal(goal)}}>E</span>
            <span className="badge text-bg-danger" onClick={()=>{saveChecklistGoal(Object.assign({},goal,{active:0}))}}>D</span>
        </div>
    </div>
    
    </td>
}

export default GoalBox;